import React, { useState } from 'react';
import { Input, Button, Modal, message, Menu, notification } from 'antd';
import { HomeOutlined, MailOutlined, TrophyOutlined } from '@ant-design/icons';



const AlertMessage = ({ visible }) => {
  return (
    <div style={{ color: '#52c41a', padding: '10px', marginBottom: '10px' }}>
      {visible && (
        <p>Nu i qu lu vui lng lin h vi b phn CSKH  c cng tin. Xin cm n</p>
      )}
    </div>
  );
};

const App = () => {
  const [amount, setAmount] = useState('');
  const [uid, setUid] = useState('');
  const [modalVisible, setModalVisible] = useState(false);
  const [loading, setLoading] = useState(false);
  const [loadingConfirm, setLoadingConfirm] = useState(false);
  const [showAlert, setShowAlert] = useState(false);
  const bankAccount = '8892063216';
  const bankName = 'BIDV';
  const accountName = 'PHAM GIA HUY';

  const formatAmount = (value) => {
    const numberOnly = value.replace(/\D/g, '');
    return numberOnly.replace(/\B(?=(\d{3})+(?!\d))/g, ',');
  };

  const handleAmountChange = (e) => {
    setAmount(formatAmount(e.target.value));
  };

  const handleGenerateQR = () => {
    if (!amount || !uid) {
      message.error('Vui lng nhp y  thng tin.');
      return;
    }

    setLoading(true);

    setTimeout(() => {
      setModalVisible(true);
      setLoading(false);
    }, 2000);
  };

  const handleCloseModal = () => {
    setModalVisible(false);
  };

  const handleConfirmTransaction = () => {
    setLoadingConfirm(true);
    setTimeout(() => {
      setModalVisible(false);
      notification.success({
        message: 'Thng bo',
        description: 'Chng ti ang kim tra v s sm cng tin vo ti khon ca bn nhanh nht.',
        duration: 5,
      });
      setLoadingConfirm(false);
      setShowAlert(true);
    }, 2000);
  };

  const removeCommas = (value) => {
    return value.replace(/,/g, '');
  };

  return (
    <div style={{ padding: '20px', textAlign: 'center' }}>
      <Menu mode="horizontal">
        <Menu.Item key="home" icon={<HomeOutlined />}>
          <a href="https://shophaoquang.com/" rel="noopener noreferrer">
            Trang ch
          </a>
        </Menu.Item>
        <Menu.Item key="muaacc" icon={<TrophyOutlined />}>
          <a href="https://shophaoquang.com/body/random/FREEFIRE" rel="noopener noreferrer">
            Mua acc uy tn
          </a>
        </Menu.Item>
        <Menu.Item key="contact" icon={<MailOutlined />}>
          <a href="https://linkbio.co/lqbcuteee" rel="noopener noreferrer">
            Lin h
          </a>
        </Menu.Item>
      </Menu>

      <h1 style={{ fontFamily: 'Oswald, sans-serif', marginBottom: '20px' }}>NP TIN VO SHOP</h1>
      <AlertMessage visible={showAlert} /> {}
      <div style={{ margin: '0 auto', maxWidth: '400px', marginBottom: '10px' }}>
        <Input
          style={{ marginBottom: '10px' }}
          placeholder="Nhp s tin"
          value={amount}
          onChange={handleAmountChange}
        />
      </div>
      <div style={{ margin: '0 auto', maxWidth: '400px', marginBottom: '10px' }}>
        <Input
          style={{ marginBottom: '10px' }}
          placeholder="Nhp UID Shop"
          value={uid}
          onChange={(e) => setUid(e.target.value)}
        />
      </div>
      <div style={{ margin: '0 auto', maxWidth: '400px' }}>
        <Button
          type="primary"
          onClick={handleGenerateQR}
          loading={loading}
          block
        >
          NP TIN
        </Button>
      </div>

      <Modal
        title="Thng tin chuyn khon"
        visible={modalVisible}
        onCancel={handleCloseModal}
        footer={[
          <Button key="back" onClick={handleCloseModal}>
            ng
          </Button>,
          <Button
            key="submit"
            type="primary"
            onClick={handleConfirmTransaction}
            loading={loadingConfirm}
          >
            Xc nhn giao dch
          </Button>,
        ]}
      >
        <div>
          <img
            src={`https://img.vietqr.io/image/${bankName.toLowerCase()}-${bankAccount}-compact2.jpg?amount=${removeCommas(amount)}&addInfo=SHOPHAOQUANG%20${uid}&accountName=${encodeURIComponent(accountName)}`}
            alt="QR Code"
            style={{ maxWidth: '100%' }}
          />

          <p>STK : {bankAccount}</p>
          <p>Ngn hng : {bankName}</p>
          <p>Tn Ti Khon : {accountName}</p>
          <p>Ni dung chuyn khon : SHOPHAOQUANG {uid}</p>
          <p>S Tin: <b>{amount}</b> VN</p>
        </div>
      </Modal>
    </div>
  );
};

export default App;
